.categoriesForm {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 30px;
  legend {
    text-align: center;
    font-weight: 300;
    font-size: 25px;
    // margin-top: 20px;
  }
  &_search {
    width: 900px;

    display: flex;
    flex-direction: column;
    align-items: center;

    gap: 10px;

    .searchInput {
      width: 100%;
    }
    .searchChecbox {
      // width: 60%;
      width: 740px;
      display: flex;
      flex-direction: row;
      padding: 0.375rem 1.25rem 0.375rem 0.75rem;

      gap: 40px;
      select {
        flex: 1;
        &:focus {
          box-shadow: none;
        }
      }
    }
  }
  &contents {
    width: 900px;
  }
}
