.adminPage {
  width: 100vw;
  height: 100vh;
  overflow-x: hidden;

  .containerDashBoard {
    // width: 100%;
    height: 100%;

    display: flex;
    flex-direction: row;
    &_menu {
      flex: 2;

      height: 100%;
      background: #162c50;
    }
    &_content {
      flex: 10;
      color: black;

      display: flex;
      justify-content: center;
      align-items: center;

      &_lists {
        width: 90%;
        height: 90%;

        // box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
      }
    }
  }
}
